<template>
  <header>
    <div class="nav">
      <div class="nav-links">
        <div
          :class="{ 'cur-page': curPage === '/home' || curPage === '/' }"
          class="link"
        >
          <router-link to="/home">HOME</router-link>
        </div>

        <div :class="{ 'cur-page': curPage === '/about' }" class="link">
          <router-link to="/about">ABOUT ME</router-link>
        </div>

        <div :class="{ 'cur-page': curPage === '/projects' }" class="link">
          <router-link to="/projects">PROJECTS</router-link>
        </div>

        <div :class="{ 'cur-page': curPage === '/experiences' }" class="link">
          <router-link to="/experiences">EXPERIENCES</router-link>
        </div>
      </div>
    </div>

    <div class="nav-icon">
      <i class="fas fa-bars" @click="clickNav()"></i>
    </div>
    <div class="nav-mob" v-if="isNavMob" @click.self="close()">
      <div class="nav-links">
        <div class="link">
          <router-link to="/home">HOME</router-link>
        </div>

        <div class="link">
          <router-link to="/about">ABOUT ME</router-link>
        </div>

        <div class="link">
          <router-link to="/projects">PROJECTS</router-link>
        </div>

        <div class="link">
          <router-link to="/experiences">EXPERIENCES</router-link>
        </div>
      </div>
    </div>

    <div class="git-link">
      <a href="https://github.com/mirro97">
        <i class="fab fa-github"></i>
      </a>
      <span class="info">mirro97의 GITHUB</span>
    </div>
  </header>
</template>

<script>
import vClickOutside from "v-click-outside";
export default {
  directives: {
    clickOutside: vClickOutside.directive,
  },
  data() {
    return {
      curPage: "/home",
      isNavMob: false,
    };
  },
  watch: {
    $route(to) {
      this.curPage = to.path;
      console.log(this.curPage);
    },
  },
  methods: {
    clickNav() {
      console.log("아이콘클릭: ", this.isNavMob);
      this.isNavMob = !this.isNavMob;
    },
    close() {
      console.log("테스트");
      this.isNavMob = false;
    },
  },
};
</script>

<style scoped>
header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 50px;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 22px;
  border-bottom: 1px solid #e6e6ed;
}

.nav {
  display: block;
}

.nav-mob {
  display: none;
}

.nav-links {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.git-link {
  position: relative;
}

.git-link:hover .info {
  z-index: 100;
  display: block;
}

.nav-icon {
  cursor: pointer;
  display: none;
}

.info {
  transition: all 250ms ease-in;
  display: none;
  position: absolute;
  right: -20px;
  bottom: -27px;
  width: max-content;
  background-color: #fff;
  padding: 3px 6px;
  font-size: 12px;
  border-radius: 4px;
  border: 1px solid #999999;
}

i {
  font-size: 26px;
  transition: all 200ms ease-in;
}

i:hover {
  color: #999999;
}

.link {
  margin-right: 26px;
}

.link:last-child {
  margin-right: 0;
}

.link a {
  height: 50px;
  line-height: 55px;
}

.cur-page::after {
  content: "";
  width: 100%;
  display: block;
  border-bottom: 2px solid #fcc419;
}

@media (max-width: 700px) {
  header {
    z-index: 2;
    position: fixed;
  }

  .nav {
    display: none;
  }

  .nav-mob {
    display: block;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 20;
    position: absolute;
    top: 50px;
    left: 0;
    height: 100vh;
    width: 100%;
  }

  .nav-mob .nav-links {
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .nav-links .link:hover {
    background-color: #fffde2;
  }

  .link {
    width: 100%;
    text-align: center;
    margin-right: 0;
  }
  .nav-icon {
    display: block;
  }
}
</style>
